/* ======================= IMPORTS ======================= */
@import url("https://fonts.googleapis.com/css2?family=Open+Sans&family=Roboto&display=swap");

/* ======================= FUNDAMENTALS ======================= */
:root {
    --row_increment: 10px;
    --gimg_width: 250px;
    --gimg_small: 26;
    --gimg_medium: 33;
    --gimg_large: 45;
}

/* ======================= TAGS ======================= */
body {
    background: #ffffff;
    font-family: "Open Sans", Calibri;
    font-size: 12pt;
    font-weight: 400;
    padding: 0px;
    margin: 0px;
    width: 80vw;
    margin: auto;
    overflow-y: scroll;
}

a {
    text-decoration: none;
}

nav {
    padding: 5px 20px;
    background: #734a08;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    align-content: flex-end;
    color: #ffffff;
    overflow: hidden;
}

input[type="text"],
input[type="email"],
select,
textarea {
    width: 95%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    resize: vertical;
}

label {
    padding: 12px 12px 12px 0;
    display: inline-block;
}

button {
    background-color: #734a08;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    float: right;
}

button:hover {
    background-color: #4b3005;
}

footer {
    background: #734a08;
    margin: 0px;
    padding: 0px;
    padding-bottom: 10px;
    width: 80vw;
    position: absolute;
    color: #ffffff;
    font-size: 10pt;
}

footer a {
    color: #ffffff;
}

footer a:hover {
    color: #cccccc;
    text-decoration: underline;
}

footer ul li {
    list-style-type: square;
    font-size: 11pt;
}

footer > div {
    display: flex;
    justify-content: center;
}

/* ======================= NOT CHANGABLE ONES ======================= */
.container {
    width: 80vw;
    margin: 0px auto;
    z-index: 1;
    margin: 10px 0px;
}

.centerize {
    display: flex;
    justify-content: center;
}

.main {
    font-size: 12pt;
}

.errordiv {
    text-align: center;
    background: rgb(192, 21, 21);
    color: white;
    font-weight: 600;
    padding: 50px;
}

.preloader {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    margin: 0;
    top: 0;
    left: 0;
    background: #666;
    position: fixed;
    z-index: 10000;
}

/* ======================= MEDIA ======================= */
@media screen and (max-width: 800px) {
    nav.responsive {
        position: relative;
    }
    nav.responsive .icon {
        position: absolute;
        float: right;
        right: 20px;
        top: 1.5vw;
    }
    nav.responsive ul {
        margin-right: 50px;
    }
    nav.responsive a {
        text-align: left;
        display: block;
    }
    nav.responsive a:not(:first-child) {
        margin-top: 10px;
    }
    nav a:not(:first-child) {
        display: none;
    }
    nav a.icon {
        float: right;
        display: block;
    }
}

@media screen and (max-width: 600px) {
    .col-25,
    .col-75,
    input[type="submit"] {
        width: 100%;
        margin-top: 0;
    }
}

/* ======================= KEYFRAMES ======================= */
@keyframes blinker {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

/* ======================= CLASSES ======================= */
.dateandtime {
    padding: 5px 20px;
    background: #734a08;
    color: #ffffff;
    font-size: 1vw;
    font-family: "Open Sans", calibri;
    margin-top: 2px;
    /*margin-bottom: 10px;*/
    display: flex;
    align-items: center;
}

.dateandtime .blinker {
    padding: 0.3vw;
    float: left;
    margin-right: 15px;
    border-radius: 50%;
    background: #ffffff;
    animation-name: blinker;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.logo {
    display: flex;
    font-size: 1.5vw;
    font-weight: 800;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-family: "Roboto", "Open Sans", calibri;
}

.logo img {
    width: 4vw;
    object-fit: cover;
}

nav ul {
    font-size: 1.5vw;
    list-style-type: none;
}

nav ul li {
    display: inline;
    margin: 0px 5px;
    color: #ffffff;
    padding: 0.5vw;
    border-radius: 4px;
}

nav ul li:hover {
    color: #ffffff;
    background: #4b3005;
}

.icon {
    display: none;
    color: #ffffff;
    font-size: 1.5vw;
}

.mainpage {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: stretch;
    align-content: stretch;
    width: 100%;
}

.mainpage div {
    min-width: 30%;
    margin:0px 5px;
}

.mainpage div h4 {
    margin-left: 10px;
}

.mainpage .ems ul {
    padding-inline-start: 0px;
}

.mainpage .ems ul li {
    font-size:1.2vw;
    background: #734a08;
    color: white;
    border-radius: 5px;
    list-style-type: none;
    padding: 5px;
    margin-top: 3px;
    border-left: 0px solid #4b3005;
    transition: 0.3s ease;
}

.mainpage .ems ul li:hover {
    border-left: 10px solid #4b3005;
    transition: 0.3s ease;
}

.about {
    width: 100%;
}

.about p {
    display: inline;
    margin: 8px;
    text-align: justify;
    text-justify: inter-word;
}

.about img {
    max-width: 40%;
    object-fit: cover;
    float: left;
    border: 4px solid #734a08;
    margin: 8px;
    float: right;
}

.about iframe {
    width: 100%;
}

.maps {
    margin: 10px auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    text-align: center;
}

.svgmap {
    width: 99%;
    height: 600px;
    border: 2px solid #734a08;
    grid-area: 1 / 1 / 2 / 2;
}

.earthmap {
    width: 99%;
    height: 600px;
    border: 2px solid #734a08;
    grid-area: 1 / 2 / 2 / 3;
}

.galleryContainer {
    margin: 0px;
    padding: 0px;
    padding-bottom: 10px;
    width: 80vw;
    display: grid;
    grid-template-columns: repeat(auto-fill, var(--gimg_width));
    grid-auto-rows: var(--row_increment);
    justify-content: center;
}

.galleryItem {
    padding: 0;
    margin: 15px 10px;
}

.galleryItem img {
    width: 100%;
    min-height: 250px;
    max-height: 250px;
    object-fit: cover;
}

.gimg_small {
    grid-row-end: span var(--gimg_small);
}

.gimg_medium {
    grid-row-end: span var(--gimg_medium);
}

.gimg_large {
    grid-row-end: span var(--gimg_large);
}

.contact-form {
    width: 50%;
    margin: auto;
    margin-bottom: 20px;
}

.bg-green {
    background-color: #2f9134;
    color: white;
    padding: 5px;
}

.bg-red {
    background-color: rgb(192, 21, 21);
    color: white;
    padding: 5px;
}

.col-25 {
    float: left;
    width: 20%;
}

.col-75 {
    float: left;
    width: 80%;
    margin-top: 6px;
}

.row {
    margin-top: 5px;
}

.row:after {
    content: "";
    display: table;
    clear: both;
}

.grid {
    display: grid;
    grid-template-columns: 50% 50%;
    justify-content: center;
    padding: 10px;
}

.credsgrid {
    display: grid;
    grid-template-columns: 10% 10%;
    text-align: center;
    margin-top: -10px;
    margin-bottom: -30px;
}

.credsgrid img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.totop {
    margin-right: 5px;
}
